@import '../../../styles/Responsive.scss';

.loader {
    width: 4rem;
    height: 4rem;
    padding: 2rem;
    border-radius: 50%;
    border: 0.5rem solid red;
    border-right: 0.5rem solid blue;

    //display: flex;
    //justify-content: center;
    //align-items: center;
    
    animation: ld-rotate 1.2s linear infinite;

    @include media(pc) {
      
    }
    
    @keyframes ld-rotate {
        0% {
          transform: rotate(0deg);
        }
        100% {
          transform: rotate(360deg);
        }
      }
}