* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    //user-select: none;
    text-decoration: none;
    order: 1px solid red;
}

*::-webkit-scrollbar {
    width: 1rem;
}   
*::-webkit-scrollbar-track {
    background-color: rgb(216, 216, 216);
}
*::-webkit-scrollbar-thumb {
    background-color: darkgrey;
    outline: 1px solid slategrey;
    border-radius: 0.5rem;
}

html {
    font-size: 62.5%;
}