@import '../../../styles/Responsive.scss';

.usercard {
    width: 70%;
    //height: 40rem;
    padding: 1rem;
    overflow: hidden;
    border-radius: 2rem;
    background-color: rgb(141, 139, 255);

    box-shadow: 3px 5px 3px 2px rgba(0, 0, 0, 0.25), -3px 5px 3px 2px rgba(0, 0, 0, 0.25);

    @include media(pc) {
        width: 35%;
        //padding: 1rem;
    }
    
    span, img, .numbers, button {
        margin: 0.5rem 0;

        @include media(pc) {
            margin: 0.6rem 0;
        }
    }

    span {
        font-size: 1.25rem;
    }

    img {
        width: 50%;
        border-radius: 50%;
        
        @include media(pc) {
            width: 40%;
        }
    }

    .numbers {
        width: 20rem;

        @include media(pc) {
        }
        
        span {
            width: 6rem;
            border: 1px solid black;
            border-radius: 0.5rem;
            padding: 0.4rem 0;

            @include media(pc) {
            }
        }
    }

    button {
        border: 1px solid black;
        border-radius: 2rem;
        padding: 0.6rem 1.4rem; 

        @include media(pc) {
        }
    }
}