@import '../../../styles/Responsive.scss';

form {
    border: 1px solid rgb(68, 111, 255);
    width: 70%;
    height: 3rem;
    margin: 6rem 0;
    grid-template-columns: 3fr 1fr;

    overflow: hidden;
    border-radius: 2rem;
    
    @include media(pc) {
        width: 50%;
        height: 3.2rem;
        margin: 6rem 0;
    }

    input{
        outline: none;

        &:first-child {
            padding: 0 1.5rem;
        }
        &:last-child {
        }
    }
}